import React from "react";
import Introduction from "../components/Introduction";
import Project from "../components/Project";
import Title from "../components/Title";

import { projects } from "../content/projects";

export default function LandingPage() {
  return (
    <>
      <main>
        <div className="flex flex-col items-center">
          <Introduction />
					<Title title={"Projects"} style={{marginTop: "2rem"}} />
          <div className="grid place-items-center grid-cols-1 md:grid-cols-2 lg:grid-cols-2 xl:grid-cols-3 gap-6 sm:max-w-sm md:max-w-4xl lg:max-w-7xl mt-8">
            {projects.map((project) => {
              return (
                <Project
									key={project.link}
									description={project.description}
									link={project.link}
                  title={project.cardTitle ?? project.title}
									thumbnail={project.images ? project.images[0].url : ""}
                />
              );
            })}
          </div>
        </div>
      </main>
    </>
  );
}
