import { navigate } from "@reach/router";
import React, { useState } from "react";
import "./Project.css";

export default function Project({backgroundColor, link, thumbnail, title}) {
  const [hovering, setHovering] = useState(false);

  const cursorStyle = {
    cursor: `pointer`,
  };

	const bgImageStyle = {
		backgroundImage: `url(${thumbnail})`,
		backgroundRepeat: "no-repeat",
		backgroundPosition: "center",
	}

  return (
		<div style={{...cursorStyle, ...bgImageStyle}} className={`${backgroundColor ?? "bg-violet-700"} flex justify-center items-center bg-cover h-64 w-80 rounded-xl`} onMouseEnter={() => setHovering(true)}  onMouseLeave={() => setHovering(false)} onClick={() => navigate("project/" + (link ?? ""))}>
			{ hovering ? (
				<p className={`bg-opacity-60 font-bold text-4xl p-1 text-center w-full pb-3 project-text`}>{title}</p>
			) : <></>
			}
		</div>
  );
}
